html, body {
  margin:0;
  padding:0;
  overflow:hidden

}
body{
  background: lightblue;
}

svg {
  position:fixed;
  bottom:0;
  left:0;
  right:0;
}


.hoverFolder:hover{
  fill: grey;
  cursor: pointer;
}

.hoverFolder{
  font-family: 'Dosis', sans-serif;
}

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}

.starterSvg{
  max-width:1000px;

  display: block;
  margin: auto
}

.clickedSvg{
  max-width:100%;

  display: block;
  margin: auto
}

.clickedSvgSmall{
  height:100%;
  width:100%;
  display: block;
  margin: auto;

}

#svg{
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
}



html,
body {
  overscroll-behavior-y: contain;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial,
    sans-serif;
  position: fixed;
  overflow: hidden;
}

#root {
  background: #192028;
  position: fixed;
  overflow: hidden;
  width: 100%;
  height: 100%;
  cursor: url('https://uploads.codesandbox.io/uploads/user/b3e56831-8b98-4fee-b941-0e27f39883ab/Ad1_-cursor.png') 39 39, auto;
}

#root > div {
  position: absolute;
  width: 100vw;
  height: 100vh;
  will-change: transform;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}


[class*="cardTopBottom-0"]{
  background-color: #FB5829 ;
  background-size: auto 85%;
  background-repeat: no-repeat;
  background-position: center center;
  width: 45vh;
  max-width: 300px;
  height: 85vh;
  max-height: 570px;
  will-change: transform;
  border-radius: 10px;
  box-shadow: 0 12.5px 100px -10px rgba(50, 50, 73, 0.4), 0 10px 10px -10px rgba(50, 50, 73, 0.3);
}

[class*="cardTopBottom-"]:not([class*="cardTopBottom-0"]) {
  background-color: #131b24;
  background-size: auto 85%;
  background-repeat: no-repeat;
  background-position: center center;
  width: 45vh;
  max-width: 300px;
  height: 85vh;
  max-height: 570px;
  will-change: transform;
  border-radius: 10px;
  box-shadow: 0 12.5px 100px -10px rgba(50, 50, 73, 0.4), 0 10px 10px -10px rgba(50, 50, 73, 0.3);
}



